import { Link } from "react-router-dom";

const badges = {
    new: 'badge-danger',
    inwork: 'badge-warning',
    complete: 'badge-success'
}

const products = {
    'course-html': '  ',
    'course-js': '  JavaScript',
    'course-vue': '  Vue JS',
    'course-php': '  PHP',
    'course-wordpress': '  WordPress',
}

const statuses = {
    'new': '',
    'inwork': ' ',
    'complete': '',
}

const TableTasks = ({tasks}) => {
    console.log(tasks);
    return ( 
        <>
            {tasks.map((task, index) => (
                <tr key={task.id}>
                    <th scope="row">{index}</th>
                    <td>{new Date(task.date).toLocaleDateString()}</td>
                    <td>{products[task.product]}</td>
                    <td>{task.name}</td>
                    <td>{task.email}</td>
                    <td>{task.phone}</td>
                    <td>
                        <div className={`badge badge-pill ${badges[task.status]}`}>{statuses[task.status]}</div>
                    </td>
                    <td>
                        <Link to={`/edit/${task.id}`}></Link>
                    </td>
                </tr>
            ))}
        </>
    );
}
//   ,        id

export default TableTasks;