import { useState } from "react";
import { serverPath } from "../../variables";
import { useNavigate } from "react-router-dom";

const EditForm = ({data}) => {
    //         .       
    const navigate = useNavigate();

    //          
    const [names, setNames] = useState(data.name);
    const [phone, setPhone] = useState(data.phone);
    const [email, setEmail] = useState(data.email);
    const [product, setProduct] = useState(data.product);
    const [status, setStatus] = useState(data.status);

    const onSubmit = (e) => {
        e.preventDefault();
        const task = {
            name: names,
            phone, 
            email, 
            product, 
            status, 
            date: data.date
        };

        //       
        fetch(serverPath + `/person/${data.id}`, {
            method: "PUT", 
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(task),
            }).then((res) => {
                if(res.ok) {
                navigate("/title");
                }
        });
    }

    //       
    const deleteTasks = () => {
        fetch(serverPath + `/person/${data.id}`, {
            method: "DELETE", 
            headers: { 'Content-Type': 'application/json' }
            }).then((res) => {
                if(res.ok) {
                navigate("/title");
                }
        });
    }

    return ( 
            <form id="form" onSubmit={(e) => {onSubmit(e)}}>
            <div className="card mb-4">
                <div className="card-header">  </div>
                <div className="card-body">
                    <div className="row mb-3">
                        <div className="col-md-2">
                            <strong>ID:</strong>
                        </div>
                        <div className="col"> <span id="number">{data.id}</span></div>
                        <input name="id" type="hidden" id="id"/>
                    </div>

                    <div className="row mb-3">
                        <div className="col-md-2">
                            <strong> :</strong>
                        </div>
                        <div className="col" id="date">
                            {new Date(data.date).toLocaleDateString()} {new Date(data.date).toLocaleTimeString()}
                        </div>
                    </div>

                    <div className="row mb-3">
                        <div className="col-md-2">
                            <strong>:</strong>
                        </div>
                        <div className="col">
                            <select id="product" name="product" className="custom-select"                                 
                                value={product}
                                onChange={(e) => setProduct(e.target.value)}>
                                <option value="course-html">  </option>
                                <option value="course-js">
                                      JavaScript
                                </option>
                                <option value="course-vue">  VUE JS</option>
                                <option value="course-php">  PHP</option>
                                <option value="course-wordpress">
                                      WordPress
                                </option>
                            </select>
                        </div>
                    </div>

                    <div className="row mb-3">
                        <div className="col-md-2">
                            <strong>:</strong>
                        </div>
                        <div className="col">
                            <input
                                type="text"
                                className="form-control"
                                id="name"
                                name="name"
                                value={names}
                                onChange={(e) => setNames(e.target.value)}
                            />
                        </div>
                    </div>

                    <div className="row mb-3">
                        <div className="col-md-2">
                            <strong>Email:</strong>
                        </div>
                        <div className="col">
                            <input
                                type="text"
                                className="form-control"
                                id="email"
                                name="email"
                                value={email}
                                onChange={(e) => {setEmail(e.target.value)}}
                                />
                        </div>
                    </div>

                    <div className="row mb-3">
                        <div className="col-md-2">
                            <strong>:</strong>
                        </div>
                        <div className="col">
                            <input
                                type="text"
                                className="form-control"
                                id="phone"
                                name="phone"
                                value={phone}
                                onChange={(e) => {setPhone(e.target.value)}}
                                />
                        </div>
                    </div>

                    <div className="row mb-3">
                        <div className="col-md-2">
                            <strong> :</strong>
                        </div>
                        <div className="col">
                            <select className="custom-select" 
                                id="status" 
                                name="status"                                 
                                value={status}
                                onChange={(e) => {setStatus(e.target.value)}}>
                                <option select="">...</option>
                                <option value="new"></option>
                                <option value="inwork"> </option>
                                <option value="complete"></option>
                            </select>
                        </div>
                    </div>
                </div>
            </div>
            <div className="row justify-content-between">
                <div className="col text-right">
                    <button type="submit" className="btn btn-primary"> </button>
                </div>
                <div className="col text-right">
                    <button type="button" className="btn btn-primary" onClick={() => {deleteTasks()}}> </button>
                </div>
            </div>
        </form>
    );
}

export {EditForm};